TABLE {
  margin: auto;
  width: 100%;
  /* Ширина таблицы */
  border: 1px solid black;
  /* Рамка вокруг таблицы */
  border-bottom: none;
  /* Убираем линию снизу */

}
TD,
TH {
  padding: 3px;
  /* Поля вокруг содержимого ячеек */
}

TH {
  text-align: left;
  /* Выравнивание по левому краю */
  background: rgb(243, 171, 171);
  /* Цвет фона */
  color: white;
  /* Цвет текста */
  border: 1px solid white;
  /* Рамка вокруг ячеек */
}

 TD {
   border-bottom: 1px solid black;
   /* Линия снизу */
 }