body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: auto;
  margin: auto;
  padding: 10px;
}

h1{
  font-size: 25px;
  text-align: center;
}

h2{
  font-size: 20px;
  text-align: center;
}
 
form{
  margin: auto;
}

span{
 display: block;
 max-width: max-content;
 margin: auto;
 margin-bottom: 20px;
}

.button{
  background-color: rgb(156, 201, 243);
  outline: none;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  color:rgb(255, 255, 255)
}

.input{
  padding: 5px 10px;
  margin-top: 5px;
  margin-bottom: 10px;
}